"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { useToast } from "@/components/ui/use-toast"

interface Table {
  id: string
  number: number
  occupied: boolean
  activeOrders?: {
    id: string
    status: string
  }[]
}

export function TableManagement() {
  const { toast } = useToast()
  const [tables, setTables] = useState<Table[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [newTableNumber, setNewTableNumber] = useState("")

  const fetchTables = async () => {
    try {
      const response = await fetch("/api/tables")
      if (!response.ok) {
        throw new Error("Failed to fetch tables")
      }
      const data = await response.json()
      setTables(data)
    } catch (error) {
      console.error("Error fetching tables:", error)
      setError("Failed to load tables. Please try again later.")
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchTables()
  }, [])

  const handleCreateTable = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      const response = await fetch("/api/tables", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          number: parseInt(newTableNumber),
        }),
      })

      if (!response.ok) {
        throw new Error("Failed to create table")
      }

      toast({
        title: "Success",
        description: "Table created successfully",
      })

      setNewTableNumber("")
      fetchTables()
    } catch (error) {
      console.error("Error creating table:", error)
      toast({
        title: "Error",
        description: "Failed to create table. Please try again.",
        variant: "destructive",
      })
    }
  }

  const handleDeleteTable = async (id: string) => {
    if (!confirm("Are you sure you want to delete this table?")) {
      return
    }

    try {
      const response = await fetch(`/api/tables?id=${id}`, {
        method: "DELETE",
      })

      if (!response.ok) {
        const data = await response.json()
        throw new Error(data.error || "Failed to delete table")
      }

      toast({
        title: "Success",
        description: "Table deleted successfully",
      })

      fetchTables()
    } catch (error) {
      console.error("Error deleting table:", error)
      toast({
        title: "Error",
        description: error instanceof Error ? error.message : "Failed to delete table. Please try again.",
        variant: "destructive",
      })
    }
  }

  if (loading) {
    return (
      <div className="animate-pulse space-y-4">
        {[1, 2, 3].map((i) => (
          <Card key={i} className="p-6">
            <div className="h-4 bg-gray-200 rounded w-1/4"></div>
          </Card>
        ))}
      </div>
    )
  }

  if (error) {
    return (
      <Card className="p-6 text-center">
        <p className="text-red-500 mb-4">{error}</p>
        <Button onClick={() => fetchTables()}>
          Try Again
        </Button>
      </Card>
    )
  }

  return (
    <div className="space-y-6">
      <Card className="p-6">
        <h2 className="text-xl font-semibold mb-4">Add New Table</h2>
        <form onSubmit={handleCreateTable} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="tableNumber">Table Number</Label>
            <Input
              id="tableNumber"
              type="number"
              value={newTableNumber}
              onChange={(e) => setNewTableNumber(e.target.value)}
              required
              min="1"
            />
          </div>
          <Button type="submit">Add Table</Button>
        </form>
      </Card>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {tables
          .sort((a, b) => a.number - b.number)
          .map((table) => (
            <Card key={table.id} className="p-6">
              <div className="flex justify-between items-start">
                <div>
                  <h3 className="font-semibold">Table {table.number}</h3>
                  <p className={`text-sm ${table.occupied ? "text-orange-500" : "text-green-500"}`}>
                    {table.occupied ? "Occupied" : "Available"}
                  </p>
                  {table.activeOrders && table.activeOrders.length > 0 && (
                    <div className="mt-2">
                      <p className="text-sm font-medium">Active Orders:</p>
                      {table.activeOrders.map((order) => (
                        <p key={order.id} className="text-sm text-gray-600">
                          Order #{order.id.slice(-4)} - {order.status}
                        </p>
                      ))}
                    </div>
                  )}
                </div>
                <Button
                  variant="destructive"
                  size="sm"
                  onClick={() => handleDeleteTable(table.id)}
                  disabled={table.occupied || (table.activeOrders && table.activeOrders.length > 0)}
                >
                  Delete
                </Button>
              </div>
            </Card>
          ))}
      </div>
    </div>
  )
}
